import { useState } from "react";
import styled from "styled-components";
import { useForm, ValidationRule } from "react-hook-form";
import lock from "../../img/lock.png";
import lockcheck from "../../img/lockcheck.png";
import { postEmailcheck, postNickcheck, postSignup } from "../../api";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { ErrorProps } from "../../state/typeDefs";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 360px;
  margin: auto;
  padding: 66px 10px 10px 10px;
`;

const SignupBox = styled.div`
  border-bottom: 2px solid rgba(0, 0, 0, 0.5);
  padding-bottom: 10px;
  width: 100%;
  margin-bottom: 10px;
`;

const SignupTitle = styled.div`
  color: black;
  font-weight: bold;
  font-size: 23px;
`;

const SignupForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: white;
`;

const Input = styled.input<ErrorProps>`
  padding: 20px 20px;
  font-size: 15px;
  border: ${(props) => (props.error ? "2px solid red" : "1px solid rgba(0,0,0,0.2)")};
  &:focus {
    outline-color: ${(props) => (props.error ? "red" : "green")};
  }
`;

const NarrowInput = styled(Input)`
  width: 80%;
`;

const WideInput = styled(Input)`
  width: 100%;
`;

const Label = styled.label`
  width: 100%;
  display: flex;
  margin-bottom: 3px;
  color: grey;
  font-weight: bold;
`;

const Wrap = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Button = styled.button`
  height: 60px;
  cursor: pointer;
  background-color: #2f6218;
  border: 0;
  outline: 0;
  color: rgb(242, 242, 242, 0.9);
  font-weight: 500;
  &:hover {
    background-color: rgba(47, 98, 24, 0.8);
  }
`;

const NarrowButton = styled(Button)`
  font-size: 15px;
  transition: 0.3s;
  padding: 0 5px;
`;

const SignupButton = styled(Button)`
  width: 100%;
  font-size: 20px;
  transition: 0.3s;
`;

interface Nickprops {
  nickValid?: boolean;
}

const Errorbox = styled.div<Nickprops>`
  color: ${(props) => (props.nickValid ? "green" : "red")};
  margin-bottom: 15px;
  font-weight: bold;
  font-size: 12px;
`;

const InputContainer = styled.div`
  position: relative;
  width: 100%;
`;

const Img = styled.img`
  position: absolute;
  top: 15px;
  right: 3px;
  width: 38px;
  height: 38px;
`;

type FormData = {
  nickname: string;
  email: string;
  certification: string | undefined;
  password: string;
  password2: string;
  address: string;
};

const Signup = () => {
  const [nickCheck, setNickCheck] = useState("");
  const [nickValid, setNickValid] = useState(false);
  const [certificationNum, setCertificationNum] = useState<string | undefined>("");
  const navigate = useNavigate();
  const {
    register,
    getValues,
    handleSubmit,
    trigger,
    formState: { errors },
  } = useForm<FormData>({ mode: "onChange" });

  const onSubmit = async (data: FormData) => {
    if (nickCheck === "") {
      Swal.fire({
        text: "  ",
        confirmButtonText: "",
        confirmButtonColor: "#2f6218",
        icon: "warning",
      });
    } else if (!nickValid) {
      Swal.fire({
        text: "  ",
        confirmButtonText: "",
        confirmButtonColor: "#2f6218",
        icon: "error",
      });
      return;
    } else {
      const { email, password, nickname } = getValues();
      await postSignup({ email, password, nickname });
      navigate("/signin");
    }
  };

  const myPattern: ValidationRule<RegExp> = {
    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
    message: "  ",
  };

  const nickPattern: ValidationRule<RegExp> = {
    value: /^[-|-|a-z|A-Z|0-9|]+$/,
    message: ", ,  ",
  };

  const passwordPattern: ValidationRule<RegExp> = {
    value: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
    message: "8 /  /  /  ",
  };

  const getNick = async () => {
    let formVerified = await trigger("nickname");
    if (formVerified) {
      const { nickname } = getValues();
      //  .   .
      // TODO:    API call  
      let valid = await postNickcheck({ nickname });
      if (valid) {
        setNickValid(true);
        setNickCheck("  ");
      } else {
        setNickValid(false);
        setNickCheck("  ");
      }
    }
  };
  //useState  .

  const postEmail = async () => {
    let formVerified = await trigger("email");
    if (formVerified) {
      const { email } = getValues();
      const certificationNumber = await postEmailcheck({ email });
      setCertificationNum(certificationNumber);
      if (!certificationNumber) {
        Swal.fire({
          text: "  ",
          confirmButtonText: "",
          confirmButtonColor: "#2f6218",
          icon: "warning",
        });
      } else {
        Swal.fire({
          title: " ",
          text: "   ,    .     .",
          confirmButtonText: "",
          confirmButtonColor: "#2f6218",
          icon: "success",
        });
      }
    }
  };

  return (
    <Container>
      <SignupBox>
        <SignupTitle></SignupTitle>
      </SignupBox>
      <SignupForm onSubmit={handleSubmit(onSubmit)}>
        <Label></Label>
        <Wrap>
          <NarrowInput
            type="text"
            error={errors.nickname?.message}
            {...register("nickname", {
              required: " ",
              pattern: nickPattern,
              minLength: { value: 2, message: " 2  " },
              maxLength: { value: 8, message: " 8  " },
              onChange: () => {
                setNickValid(false);
                setNickCheck("");
              },
              shouldUnregister: true,
            })}
          />
          <NarrowButton type="button" onClick={getNick}>
            
          </NarrowButton>
        </Wrap>
        <Errorbox nickValid={nickValid}>{errors.nickname?.message ? errors.nickname.message : nickCheck}</Errorbox>
        <Label></Label>
        <Wrap>
          <NarrowInput
            type="text"
            error={errors.email?.message}
            {...register("email", {
              required: " ",
              pattern: myPattern,
            })}
          />
          <NarrowButton type="button" onClick={postEmail}>
            
          </NarrowButton>
        </Wrap>
        <Errorbox>{errors.email?.message}</Errorbox>
        <Label></Label>
        <InputContainer>
          <WideInput
            type="text"
            error={errors.certification?.message}
            {...register("certification", {
              required: " ",
              validate: {
                matchPassword: (value: string | undefined) => {
                  return certificationNum === value || "  .";
                },
              },
            })}
          />
        </InputContainer>
        <Errorbox>{errors.certification?.message}</Errorbox>
        <Label></Label>
        <InputContainer>
          <WideInput
            type="password"
            error={errors.password?.message}
            {...register("password", {
              required: " ",
              pattern: passwordPattern,
            })}
          />
          <Img src={lock} />
        </InputContainer>
        <Errorbox>{errors.password?.message}</Errorbox>
        <Label> </Label>
        <InputContainer>
          <WideInput
            type="password"
            error={errors.password2?.message}
            {...register("password2", {
              required: " ",
              validate: {
                matchPassword: (value) => {
                  const { password } = getValues();
                  return password === value || "  .";
                },
              },
            })}
          />
          <Img src={lockcheck} />
        </InputContainer>
        <Errorbox>{errors.password2?.message}</Errorbox>
        <SignupButton type="submit"></SignupButton>
      </SignupForm>
    </Container>
  );
};
export default Signup;
